@use "./../defines.scss" as defines;

:root {
  --index: calc(1vw + 1vh)
}

.wrapper_donate {
  display: inline-flex;
  align-items: center;
  font-weight: 600;
  padding: 20px 40px 20px;
  border-radius: 51px;
  gap: 20px;
  flex-direction: column;
  background-color: defines.$primaryColor;
  box-shadow: 0px 13px 7.699999809265137px 0px #0000004d;

  span {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    font-size: 30px;
  }

  input {
    padding: 30px 105px 30px 40px;
    text-indent: 30px;
    width: 389px;
    text-align: right;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    font-weight: 600;
    font-size: 50px;
    border: none;
    border-radius: 44px;
    outline: none;
  }

  &__input {
    position: relative;
    display: inline-block;
  }

  &__input::after {
    content: "";
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    font-weight: 600;
    font-size: 50px;
    padding-left: 30px;
    position: absolute;
    top: 48%;
    right: 20px;
    /*     */
    transform: translateY(-50%);
  }

  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}

.wrapper_buttons {
  display: flex;
  gap: 20px;

  & button {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    font-weight: 600;
    font-size: 30px;
    background-color: white;
    border: 3px solid white;
    border-radius: 10px;
    padding: 3px 10px 3px 10px;
    transition: 0.5s;

    &:hover {
      // box-shadow: 0px 0px 20px black;
      border-color: #39b8ff;
    }
  }
}

.wrapper_buttons__pay {
  width: 100%;
  box-shadow: 70px #78c5f0b0;
  transition: 0.5s;
  border-radius: 100px;

  a {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 30px;
    font-weight: 700;
    background-color: deepskyblue;
    padding: 4px 100px 4px 100px;
    color: white;
    border: none;
    border-radius: 100px;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    overflow: hidden;
  }

  & img {
    padding-left: 10px;
    width: 40px;
  }

  &:hover {
    box-shadow: 0px 0px 20px deepskyblue;
  }
}

@media(max-width: 1440px) {
  .wrapper {
    &_donate {
      width: 400px;

      &__input {

        input {
          width: 100%;
          height: 80px;
        }
      }
    }

    &_buttons {
      &__pay {

        a {
          display: inline-block;
          min-width: 80px;
        }

        img {
          display: none;
        }
      }
    }
  }
}

@media(max-width: 1120px) {
  .wrapper {
    &_donate {

      input {
        font-size: 35px;
        padding-right: 75px;
        height: 75px;
      }

      &__input {
        &::after {
          font-size: 35px;
        }
      }
    }
  }
}

@media (max-width: 780px) {
  .wrapper {
    &_donate {
      width: 90vw;

      span {
        font-size: calc(var(--index) * 2.3);
      }

      &__input {
        &::after {
          font-size: calc(var(--index) * 2.7);
        }

        input {
          width: 100%;
          height: max-content;
          padding: calc(var(--index) * 1.8) calc(var(--index) * 6.2);
          font-size: calc(var(--index) * 2.7);
        }
      }
    }

    &_buttons {
      &__pay {

        a {
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: calc(var(--index) * 2.1);
          min-width: 80px;
        }

        img {
          display: none;
        }
      }
    }
  }

  .wrapper_buttons {
    display: flex;
    gap: calc(var(--index) * 1.6);

    & button {
      font-size: calc(var(--index) * 2.1);
      padding: 3px 16px 3px 16px;
    }
  }
}

@media(max-width: 498px) {
  .wrapper {
    &_donate {
      padding: 20px 15px;
      width: 90vw;
      border-radius: 40px;
      span{
        font-size: calc(var(--index) * 2.4);
      }

      &__input {
        &::after {
          font-size: calc(var(--index) * 3.4);
        }

        input {
          height: max-content;
          padding: calc(var(--index) * 2.5) calc(var(--index) * 7.9);
          font-size: calc(var(--index) * 3.5);
        }
      }
    }

    &_buttons {
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;

      &__pay {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;

        a {
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: calc(var(--index) * 2.3);
          padding: 10px 0;
          min-width: 0;
          max-width: 90%;
        }
      }
    }
  }

  .wrapper_buttons {
    display: flex;
    gap: calc(var(--index) * 1.6);

    & button {
      font-size: calc(var(--index) * 2.4);
      padding: 3px 16px 3px 16px;
    }
  }
}

@media (max-width: 382px) {
  .wrapper {
    &_donate {
      padding: 20px 15px;
      width: 94vw;
    }
  }

  .wrapper_buttons {
    display: flex;
    flex-wrap: nowrap;
    gap: calc(var(--index) * 1);

    & button {
      font-size: calc(var(--index) * 2.4);
      padding: 3px 16px 3px 16px;
    }
  }
}


@media(max-width: 280px) {
  .wrapper {
    &_donate {
      gap: 7px;

      &__input {
        input {
          font-size: 25px;
        }

        &::after {
          font-size: 25px;
        }
      }
    }

    &_buttons {
      height: fit-content;
      gap: 5px;

      button {
        font-size: 20px;
      }

      &__pay {
        a {
          font-size: 20px;
        }
      }
    }
  }
}